
h1[data-v-2]{color:#42b983
}
.red[data-v-2]{color:red
}
.list[data-v-2]{width:50%;min-width:300px;font-size:0.8rem;text-align:left;margin-left:auto;margin-right:auto
}
.list .item a[data-v-2]{transition:all 0.7s ease
}
.list .item[data-v-2]:not(.active){color:gray
}
.list .item:not(.active) a[data-v-2]{color:#aca !important
}
.list .item input[data-v-2]{margin-right:5px
}
.right.bar[data-v-2]{position:fixed;z-index:9998;top:0;left:0;bottom:0;width:10px;width:1vmax;border-right:1px solid green;background-color:green;background-image:linear-gradient(red, #ff0, green)
}
.right.bar .door[data-v-2]{position:fixed;z-index:9999;top:0;background-color:white;width:10px;width:1vmax;border-top:1px solid green;border-right:1px solid green;border-radius:0 0 3px 3px;transition:bottom 0.5s ease
}
p.choice[data-v-2]{font-size:1rem
}
p.choice input[data-v-2]{margin-left:10px;margin-right:3px;position:relative;top:-1px
}
i.icon[data-v-2],i.icon[data-v-2]::before{text-decoration:none
}
i.chevron.right.icon[data-v-2]{animation:dada 1s ease 3
}
@keyframes dada{
0%{position:relative;left:0
}
50%{position:relative;left:0.5em
}
100%{position:relative;left:0
}
}



