
html{height:100%;font-size:18px;font-size:3vm;font-size:3vmin
}
body{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:100%
}
#app{color:#2c3e50;max-width:600px;font-family:Source Sans Pro, Helvetica, sans-serif;font-size:1rem;text-align:center
}
#app a,#app nav .ui.dropdown.simple.item{color:#42b983;text-decoration:none
}
.logo{width:100px;height:100px
}
#main{position:absolute;top:150px;left:0;width:100%
}
#app nav{position:fixed;z-index:999;top:0;left:0
}
#app nav a{transition:all 0.5s ease
}
#app nav a.router-link-active{background-color:#ccf !important
}
.gray{color:gray;font-size:0.618em
}
.fade-enter-active,.fade-leave-active{transition:opacity 0.5s ease-in
}
.fade-enter,.fade-leave-active{opacity:0
}



